import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '../store'
import { getToken } from '@/utils/auth'

//axios
const baseURL =
  process.env.NODE_ENV === 'production'
    ? 'http://192.168.0.105/index.php/workbench'
    : // ? 'https://lyqq.cn-lianyi.com/index.php/workbench'
    // 'http://192.168.0.15/index.php/workbench'
    'http://192.168.0.105/index.php/workbench'
// 'https://lyqq.cn-lianyi.com/index.php/workbench'

// axios
const service = axios.create({
  baseURL: baseURL, // api  base_url
  headers: {
    post: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  },
  timeout: 5000 // 
})

// request
service.interceptors.request.use(
  config => {
    if (store.getters.token) {
      config.headers['X-Token'] = 't' + getToken() // token 
    }
    return config
  },
  error => {
    // Do something with request error
    // console.log(error) // for debug
    Promise.reject(error)
  }
)

// response 
service.interceptors.response.use(
  response => {
    /**
     * token
     */
    if (response.headers.token) {
      store
        .dispatch('RefreshToken', response.headers.token)
        .then(() => { })
        .catch(() => { })
    }
    /**
     * code0 
     */
    const res = response.data
    const headers = response.headers

    if (typeof headers.code !== 'undefined') {
      if (headers.code === '50016') {
        Message({
          message: ' ',
          type: 'error',
          duration: 2 * 1000
        })
      }
      // 50008:token; 50014:Token ;
      if (headers.code === '50008' || headers.code === '50014') {
        MessageBox.confirm('token', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload() // vue-router bug
          })
        })
      }
      return Promise.reject(headers.code)
    }

    if (res.code !== 0) {
      Message({
        message: res.msg,
        type: 'error',
        duration: 2 * 1000
      })
      return Promise.reject(res.code)
      // return Promise.reject('error')
    } else {
      return response.data
    }
  },
  error => {
    // console.log('err' + error) // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service
