import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
Vue.config.productionTip = false

import ElementUI from 'element-ui' // UI
import 'element-ui/lib/theme-chalk/index.css' // UICSS
import locale from 'element-ui/lib/locale/lang/zh-CN' // UI
Vue.use(ElementUI) //ui
import 'normalize.css/normalize.css' // css
import '@/assets/less/index.less' //less
import '@/assets/icons' // incon iconfont svg
import '@/permission' // 
import Echart from '@/utils/echart.js' //echat 
Vue.use(Echart) //echart
import xlsx from '@/utils/xlsx.js'
Vue.use(xlsx)
import config from '@/utils/config.js'
Vue.use(config)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
